.library {
	position: fixed;
	top: 0;
	left: 0;
	width: 20rem;
	height: 100%;
	box-shadow: 2px 2px 50px rgb(204, 204, 204);
	overflow: scroll;

	h2 {
		padding: 2rem;
	}
	transform: translateX(-100%);
	transition: all 0.5s ease;
	opacity: 0;
	background-color: #ffffff;
}
.library-song {
	display: flex;
	align-items: center;
	cursor: pointer;
	transition: background 0.4s ease;
	padding: 1rem 2rem;
	img {
		width: 30%;
	}
	&:hover {
		background: rgb(222, 222, 225);
	}
}
.song-description {
	padding-left: 1rem;
	h3 {
		font-size: 1rem;
	}
	h4 {
		font-size: 0.5rem;
	}
}

* {
	scrollbar-width: thin;
	scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}
*::-webkit-scrollbar {
	width: 5px;
}
*::-webkit-scrollbar-track {
	background: transparent;
}
*::-webkit-scrollbar-thumb {
	background-color: rgba(155, 155, 155, 0.5);
	border-radius: 20px;
	border: transparent;
}
.selected {
	background: rgb(165, 181, 228);
}
.active-library {
	transform: translateX(0%);
	opacity: 1;
}
@media screen and (max-width: 768px) {
	.library {
		top: 10vh;
		width: 100%;
		box-shadow: none;
		opacity: 1;
	}
}
